<template>
  <div class="module-container">
    {{content}}
    <ul class="ul-style-no">
      <li v-for="article in articles" v-bind:key="article.name">
        <div>
          <h3>
            <router-link class="card-link" :to="'/article_render?group=' + article.group + '&number=' + article.number"><span>{{'[ ' + article.tag + ' ]' + ' ' + article.name}}</span></router-link>
          </h3>
          <span class="display-block">{{article.date}}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Axios from 'axios'
export default {
  name: 'article-list',
  data () {
    return {
      content: 'This is article-list component.',
      articles: [
        {
          // 文章的编号
          number: '',
          // 文章的组名
          group: '',
          // 文章的文件名
          name: '',
          // 文章的日期
          date: '',
          // 文章的标签
          tag: ''
        }
      ]
    }
  },
  beforeCreate: function () {
    // 初始化博客文章列表
    Axios.get('config.html').then(response => {
      // 获取以‘---’分割的部分
      let _groups = response.data.split('---')
      for (let index = 0; index < _groups.length; index++) {
        // 剔除掉多余的空格或空字符
        if (_groups[index].length > 1) {
          let _group = _groups[index].split('\n').filter(value => { return value !== '' && value !== undefined })
          for (let indexOf = 1; indexOf < _group.length; indexOf++) {
            let _buffer = _group[indexOf].split('-')
            let _obj = {
              number: _buffer[0].replace(' ', '').trim(),
              group: _group[0],
              name: _buffer[1].replace(' ', '').trim(),
              date: _buffer[2].replace(' ', '').trim(),
              tag: 'Vue.js'
            }
            this.articles.push(_obj)
          }
        }
      }
      // 删除第一个默认的元素
      this.articles.splice(0, 1)
    })
    .catch(response => {
      this.content = '<h1>' + response.statusText + '</h1>'
    })
  }
}
</script>

